import React, {useEffect, useState} from 'react';
import SectionSlider from "../components/SectionSlider/SectionSlider";
import {useFetching} from "../hooks/useFetching";
import FilmService from "../API/FilmService";

const MainPage = () => {
    const [topNames, setTopNames] = useState([
        'TOP_250_BEST_FILMS',
        'TOP_100_POPULAR_FILMS',
        'TOP_AWAIT_FILMS',
    ])

    const [filmsKP, setFilmsKP] = useState([]);
    const [filmsPopular, setFilmsPopular] = useState([]);
    const [filmsAwait, setFilmsAwait] = useState([]);
    const [page, setPage] = useState(1);

    const [fetchFilms, isFilmsLoading, filmError] = useFetching(async () => {
        for(let topName of topNames) {
            const response = await FilmService.getTop(topName);
            switch (topName) {
                case 'TOP_250_BEST_FILMS': {
                    response.data.films.pop();
                    setFilmsKP([...response.data.films])
                }
                break;
                case 'TOP_100_POPULAR_FILMS': {
                    response.data.films.pop();
                    setFilmsPopular([...response.data.films])
                }
                break;
                case 'TOP_AWAIT_FILMS': {
                    setFilmsAwait([...response.data.films])
                }
                break;
            }
        }
    })

    useEffect(() => {
        fetchFilms(page);
    }, [page])

    return (
        <div style={{paddingBottom: '40px'}}>
            <div>
                <SectionSlider
                    title=" 250 "
                    films={filmsKP}
                    loading={isFilmsLoading}
                />
                <SectionSlider
                    title=" 100  "
                    films={filmsPopular}
                    loading={isFilmsLoading}
                />
                <SectionSlider
                    title="  "
                    films={filmsAwait}
                    loading={isFilmsLoading}
                />
            </div>
        </div>
    );
};

export default MainPage;