import React, {useState, useEffect, forwardRef} from 'react';
import classes from "./FilmFullStyle.module.css";
import {useFetching} from "../../hooks/useFetching";
import FilmService from "../../API/FilmService";
import {useNavigate} from "react-router";

const FilmFullItem = forwardRef(({film}, ref) => {
    const router = useNavigate();
    return (
        <div className={classes.film__item} ref={ref}>
            <img
                onClick={() => router(`/${film.kinopoiskId}`)}
                className={classes.film__poster}
                alt=""
                src={`https://kinopoiskapiunofficial.tech/images/posters/kp/${film.kinopoiskId}.jpg`}
            />
        </div>
    );
});

export default FilmFullItem;