import React, {useEffect, useRef, useState} from 'react';
import classes from "./Carousel.module.css";

const Carousel = ({children, ...props}) => {
    const [positionX, setPositionX] = useState(0);
    const [isPrevBtn, setIsPrevBtn] = useState('0');
    const [isNextBtn, setIsNextBtn] = useState('1');

    const itemWidth = 220;

    const endOfList = (props.numberOfItems - props.numberOfItemsShow) * itemWidth;

    function changePositionX(e) {
        if (e.target.id === "prev") {
            if (positionX >= 0) {
                setPositionX(0);
            } else {
                setPositionX(positionX + itemWidth);
            }
        } else {
            if (positionX > -endOfList) {
                setPositionX(positionX - itemWidth);
            }
        }
    }

    useEffect(() => {
        if (positionX === 0) {
            setIsPrevBtn('0');
        } else if (positionX < 0 && positionX > -endOfList) {
            setIsPrevBtn('1');
            setIsNextBtn('1');
        } else if (positionX <= -endOfList) {
            setIsNextBtn('0');
        }
    }, [positionX]);

    return (
        <div className={classes.carousel__wrapper}>
            <button
                style={{opacity: isPrevBtn}}
                id="prev"
                className={classes.prevBtn}
                onClick={changePositionX}
            />
            <div className={classes.carousel} style={{transform: `translateX(${positionX}px)`}}>
                {children}
            </div>
            <button
                style={{opacity: isNextBtn}}
                id="next"
                className={classes.nextBtn}
                onClick={changePositionX}
            />
        </div>
    );
};

export default Carousel;