import React from 'react';
import FilmItem from "./FilmItem";
import classes from "./SectionSlider.module.css";
import {ReactComponent as Arrow} from "../../assets/icons/arrow-small-right-free-icon-font.svg";

const FilmList = ({films, style}) => {
    return (
        <div className={classes.film__list} style={style}>
            {films.map(film =>
                <FilmItem key={film.filmId} film={film}/>
            )}
            {films.length > 5 && <div className={classes.showAllBtn__wrapper}>
                 <p className={classes.showAllBtn__text}> </p>
                <Arrow className={classes.showAllBtn}></Arrow>
            </div>}
        </div>
    );
};

export default FilmList;